import moment from "moment";

class BookingDataService {

    bookingStatusAttrsForUser(status, booking) {
        switch (status) {
            case "TENTATIVE":
                return {
                    color: "cyellow",
                    icon: "mdi-clock",
                    status: "Foglals alatt",
                    status_admin: "j",
                    action_admin_icon_ok: "fa-calendar-check",
                    action_admin_ok: "Megersts",
                    action_admin_icon_cancel: "fa-ban",
                    action_admin_cancel: "Elutasts",
                    info_admin:"Ez egy nemrg ("+ moment(booking.transitions[booking.transitions.length-1].created).locale("hu").fromNow()+") ltrehozott foglals. Erstsd meg " + booking.user.lastname + " " + booking.user.firstname + " foglalst, hogy utna befizethesse a foglalt! Ha esetleg nem megfelel az idpont, utastsd el.",
                    info: "Foglalsod sikeresen elmentettk, jelenleg feldoglozs alatt van. A nyaral tulajdonosa 1-2 napon bell visszajelez, errl emailben is rtesteni fogunk!"
                };
            case "BOOKED":
                return {
                    color: "corange",
                    icon: "mdi-book",
                    status: "Lefoglalva, foglalra vr",
                    status_admin: "Foglalra vr",
                    action_admin_icon_ok: "fa-check-double",
                    action_admin_ok: "Foglal kifizetve",
                    action_admin_icon_cancel: "fa-ban",
                    action_admin_cancel: "Elutasts",
                    info_admin:"A foglalst visszaigazoltad, jelenleg a vendgre vrunk, hogy befizesse a foglalt. Ha " + booking.user.lastname + " " + booking.user.firstname + " mr elutalta a foglalt, erstsd ezt meg! Ha tbb mint egy hete nem fizetett foglalt utastsd el ezt a foglalst.",
                    info: "A nyaral tulajdonosa elfogadta a foglalsod. A foglals vglegestshez egy hten bell foglalt kell fizetned. Ezt gy meg gy lehet megtenni. Krds esetn rj emailt, vagy hvj fel minket telefonon!"
                };
            case "PAID":
                return {
                    color: "clightgreen",
                    icon: "mdi-checkbox-marked-circle",
                    status: "Lefoglalva, foglal kifizetve",
                    status_admin: "Foglal kifizetve",
                    info_admin:"Ezzel a foglalssal jelenleg nincs teend. A foglalst mr visszaigazoltad s a foglal is befizetsre kerlt.",
                    info: "A foglals vglegestve lett, vrunk szeretettel " + moment(booking.arrival).locale("hu").fromNow() + " (" + moment(booking.arrival).locale("hu").format("LL") + ") ! :)"
                };
            case "DELETED":
                return {
                    color: "red",
                    icon: "mdi-close-circle",
                    status: "Trlve",
                    status_admin: "Trlve",
                    info_admin:"Trlt vagy lemondott foglals",
                    info: "A foglalsod trlve."
                };
            case "OUTDATED":
                return {
                    color: "purple",
                    icon: "mdi-timer-sand-empty",
                    status: "Lejrt",
                    status_admin: "Lejrt",
                    info_admin:"Ez egy rgebbi, mr nem aktulis foglals.",
                    info: "Ez egy rgebbi, mr nem aktulis foglalsod"
                };
            default:
                return {
                    color: "error",
                    icon: "mdi-emoticon-sad",
                    status: "Error ",
                    status_admin: "Error",
                    info_admin:"",
                    info: ""
                };
        }
    }
}
export default new BookingDataService();